.tabs-container {
  display: flex;
  flex-direction: column;
  position: relative;
  word-break: break-all;
  /*border: 1px solid rgba(0, 0, 0, 0.274);*/
}

.bloc-tabs {
  display: flex;
}
.tabs {
  padding: 15px;
  text-align: center;
  width: 50%;
  background: rgba(128, 128, 128, 0.075);
  cursor: pointer;
  border-bottom: 1px solid rgba(0, 0, 0, 0.274);
  box-sizing: content-box;
  position: relative;
  outline: none;
}
/*
.tabs:not(:last-child){
  border-right: 1px solid rgba(0, 0, 0, 0.274);
}*/

.active-tabs {
  background: white;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.active-tabs::before {
  content: "";
  display: block;
  position: absolute;
  top: -5px;
  left: 50%;
  transform: translateX(-50%);
  width: calc(100% + 2px);
  height: 5px;
  background: rgb(88, 147, 241);
}
/*
button {
  border: none;  
}*/
.content-tabs {
  flex-grow : 1;
}
.content {
  padding: 20px;
  width: 100%;
  height: 100%;
  display: none;
}
.content h2 {
  padding: 0px 0 5px 0px;
}
.content p {
  width: 100%;
  height: 100%;
}
.active-content {
  display: block;
}

@media(max-width: 355px){
  .xsm-phone{
    display: none;
  }
}